@import '../../../scss/main.scss';

.section05{
 
    width: 100%;
    height: 592px;
    display: flex;
    flex-direction: column;
    padding-left: 150px;
    overflow: hidden;
    position: relative;
    z-index: 999;
    margin-top: 7rem;
  
   

    .container-sect05{

        width: 95%;
    
    
        .section05-news{
            width: 100%;
            height: 20%;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            font-family: 'Roboto', sans-serif;
            margin: 15px 0px 0px 10px;


            h5{
                color: $secondary-blue-900;
                font-size: 40px;
                line-height: 51px;
                margin: 0px;
            }

            .link-all-news{
                padding: 15px 50px 0px 0px;
                font-size: 16px;
                line-height: 19px;
                color: $primary-500;
                text-decoration-line: underline;
                text-transform: uppercase;
            }

        }

        .container-cards-news{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        
        }

        .timer{
            width: 90%;
            margin: 30px 0px 0px 10px;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;

            .timer-01{
                width: 500px;
                height: 8px;
                background: $primary-500;
            }
            .timer-02{
                width: 700px;
                height: 7px;
                background: #FFD980;
            }
        }
    }
}

@media (max-width: 375px){

    .section05{
 
        width: 100%;
        width: 90%;
        padding: 70px 0px 0px 0px;

    
        .container-sect05{
    
            width: 100%;
            height: 100%;
        
        
            .section05-news{
                width: 100%;
                height: 15%;
                flex-direction: column;
                margin-bottom: 1rem;
               
    
                h5{
                    font-size: 28px;
                    line-height: 36px;
                    margin: 0px;
                }
    
                .link-all-news{
                    padding: 0px;
                    font-size: 14px;
                    line-height: 18px;
                }
    
            }
    
            .timer{
    
                .timer-01{

                    height: 4px;

                }
                .timer-02{

                    height: 4px;

                }
            }
        }
    }

}